import { HtmlProcessingInstruction } from '../HtmlProcessingInstruction';
import React from 'react';

/**
 * Disabling antiforgery token on all forms with action
 */
export const buildFormBuilderForm = (): HtmlProcessingInstruction => ({
    shouldProcessNode: function (node: any) {
        return node.name === 'form' && node.attribs.action && node.attribs.action.startsWith('/formbuilder');
    },
    processNode: function (node, children) {
        console.log('processing form with action');
        const newAttrs = { ...node.attribs };
        newAttrs['asp-antiforgery'] = 'false';
        return React.createElement(
            node.name,
            {
                ...newAttrs,
            },
            children
        );
    },
});
